import React from "react";
import { Layout, Menu, Row } from "antd";
import { Link } from "react-router-dom";

//import Icons from '../assets/icons/Icon';
import {
  hotline,
  openline,
  polls,
  manageemp,
  broadcastmsg,
  help,
  info,
} from "../assets/icons/Icon";
const { Sider } = Layout;

const OrgSidebar = props => {
  return (
    <Sider>
      <Menu theme="dark" mode="inline" defaultSelectedKeys={["4"]}>
        <Menu.Item
          key="1"
          icon={
            <span className="icon-circle">
              <span className="icon icon-hotlines" />
            </span>
          }
        >
          {" "}
          <Link to="/hotlinedirectory">Hotlines</Link>
        </Menu.Item>
        <Menu.Item
          key="2"
          icon={
            <span className="icon-circle">
              <span className="icon icon-openlines" />
            </span>
          }
        >
          {" "}
          Openlines
        </Menu.Item>
        <Menu.Item
          key="3"
          icon={
            <span className="icon-circle">
              <span className="icon icon-polls" />
            </span>
          }
        >
          {" "}
          Polls
        </Menu.Item>
        <Menu.Item
          key="4"
          icon={
            <span className="icon-circle">
              <span className="icon icon-manage-emp" />
            </span>
          }
        >
          {" "}
          <Link to="/employee">Manage Employees</Link>
        </Menu.Item>
        <Menu.Item
          key="5"
          icon={
            <span className="icon-circle">
              <span className="icon icon-broadcast" />
            </span>
          }
        >
          {" "}
          Broadcast Messages
        </Menu.Item>
        <Menu.Item
          key="6"
          icon={
            <span className="icon-circle">
              <span className="icon icon-help" />
            </span>
          }
        >
          {" "}
          Help
        </Menu.Item>
        <Menu.Item
          key="7"
          icon={
            <span className="icon-circle">
              <span className="icon icon-info" />
            </span>
          }
        >
          {" "}
          Info
        </Menu.Item>
      </Menu>
      <div className="btm-div">
        <a href="#" className="px-2">
          Info
        </a>
        <a href="#" className="px-2">
          Help
        </a>
      </div>
    </Sider>
  );
};

export default OrgSidebar;
