import React from "react";
import { Layout, Menu, Row } from "antd";
import { Link } from "react-router-dom";
const { Sider } = Layout;

const AmWebSidebar = () => {
  return (
    <Sider>
      <Menu theme="dark" mode="inline" defaultSelectedKeys={["1"]}>
        <Menu.Item
          key="1"
          icon={
            <span className="icon-circle">
              <span className="icon icon-hotlines" />
            </span>
          }
        >
          {" "}
          <Link to="/hotlinemessages">Hotlines Messages</Link>
        </Menu.Item>
        <Menu.Item
          key="2"
          icon={
            <span className="icon-circle">
              <span className="icon icon-openlines" />
            </span>
          }
        >
          {" "}
          Hotline Reports
        </Menu.Item>
        <Menu.Item
          key="3"
          icon={
            <span className="icon-circle">
              <span className="icon icon-polls" />
            </span>
          }
        >
          {" "}
          Openline Messages 
        </Menu.Item>
        <Menu.Item
          key="4"
          icon={
            <span className="icon-circle">
              <span className="icon icon-manage-emp" />
            </span>
          }
        >
          {" "}
          Openline Reports
                  </Menu.Item>
        <Menu.Item
          key="5"
          icon={
            <span className="icon-circle">
              <span className="icon icon-broadcast" />
            </span>
          }
        >
          {" "}
          Poll Results
        </Menu.Item>
        <Menu.Item
          key="7"
          icon={
            <span className="icon-circle">
              <span className="icon icon-info" />
            </span>
          }
        >
          {" "}
          Broadcast Messages
        </Menu.Item>
      </Menu>
      <div className="btm-div">
        <a href="/" className="px-2" disabled>
          Info
        </a>
        <a href="/" className="px-2" disabled>
          Help
        </a>
      </div>
    </Sider>
  );
};

export default AmWebSidebar;
